.App {
  text-align: center;
}

h2 {
  margin-top: 6vh;
}

.Placeholder {
  min-height: 6vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App__title {
  margin-top: 5rem;
  font-size: 50px;
}

.App__buttonPanel {
  display: flex;
  justify-content: center;  
  
}

.App__Header-Button {
  margin: 1rem;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  text-decoration: none;
}



.techStack__Icon {
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: center;
}

.frontendwrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;  
}


.App__portrait {  
  height: 15rem;
}

.App__Project-Wrapper {
  margin: 5rem;
  display: flex;  
  flex-flow: row wrap;
  justify-content: space-between;
}

.App__Project-Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
}

.App__project-image {
  height: 15rem;
  width: 15rem;
}

.App__Project-Tech-Wrapper{
  display: flex;

}

.App__Project-Tech-Item {
  margin-right: 0.5rem;
  padding: 5px;
  border-radius: 5px;
  background-color: #787978;
}

.svg {
  height: 20%;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

ul {
  list-style-type: none;
 }

 .ProjectCard {
   box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
   max-width: 32rem;
   min-width: 32rem;
   max-height: 32rem;
   min-height: 32rem;
   margin: 10px 30px;
 }

 .ProjectWrapper {
   display: flex;
   flex-direction: row;
 }

.Project__Card-Section-Title {
  font-weight: bold;
  margin-right: 3px;
}

.Project__Card-Section-Wrapper {
  display: flex;
  justify-content: center;
}

.Project__Link-Wrapper {
  display: flex;
  justify-content: space-around;
}

.Project__Link {
  border: black solid 1px;
  padding: 5px;  
}

 .bigpwrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
  justify-content: center;
 }

 .App__Copyright {
   margin-top: 5rem;
 }

 