/*SVG Waves*/
.wavies > use {
    animation: waveAround 14s linear infinite;
  }
  .wavies > use:nth-child(1) {
    animation-delay: 2s;
  }
  .wavies > use:nth-child(2) {
    animation-delay: 3s;
    animation-duration: 8s;
  }
  .wavies > use:nth-child(3) {
    animation-delay: -3s;
    animation-duration: 8s;
  }
  @keyframes waveAround {
    0% {
      transform: translate(-90px, 0%);
    }
    100% {
      transform: translate(85px, 0%);
    }
  }
  
  /* SECTION LAYOUT  */
  .section-bubble,
  .section-bubble1,
  .section-bubble2,
  .section-bubble3,
  .section-bubble4,
  .section-bubble_type-a,
  .section-bubble_type-b {
    margin-bottom: calc(100vw / 5);
    position: relative;
  }
  .section-bubble:after,
  .section-bubble1:after,
  .section-bubble2:after,
  .section-bubble3:after,
  .section-bubble4:after,
  .section-bubble_type-a:after,
  .section-bubble_type-b:after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: calc(100vw / 5);
    background-size: 100%;
    transform: translate(0, 100%);
  }
  
