  /*  COFFEE SVG  */
  .coffee {
    padding: 20px 0;
  }
  
  .coffee-container {
    height: 300px;
  }
  
  #coffee-wrap {
    width: 800px;
    height: 350px;
    position: absolute;
    top: 70%;
    left: 50%;
    z-index: 1;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  
  .mug,
  .machine,
  .floor {
    position: absolute;
  }
  
  .mug {
    width: 72px;
    height: 77px;
    bottom: 6px;
    z-index: 4;
  }
  
  .mug1 {
    opacity: 1;
    right: 100px;
    animation: new-mug 3.5s ease infinite;
  }
  
  .mug2 {
    opacity: 1;
    right: 220px;
    animation: jump-to-machine 3.5s linear infinite;
  }
  
  .mug3 {
    right: 377px;
    bottom: 46px;
    animation: filled-out 3.5s ease infinite;
  }
  
  .mug4 {
    right: 540px;
    animation: go-out 3.5s ease infinite;
  }
  
  .svg-mug {
    width: 100%;
    height: 100%;
  }
  
  .mug .cream {
    animation: mug-fill-cream 3.5s ease infinite;
  }
  
  .mug .coffee {
    animation: mug-fill-coffee 3.5s ease infinite;
  }
  
  .machine {
    width: 173px;
    height: 285px;
    bottom: 6px;
    left: 300px;
    z-index: 2;
    transform: scale(1);
    transform-origin: bottom center;
    animation: machine-coffee 3.5s ease infinite;
  }
  
  #hot-coffee-wrap {
    position: absolute;
    bottom: 56px;
    right: 408px;
    height: 100px;
    width: 20px;
    overflow: hidden;
    z-index: 2;
    animation: hot-coffee-wrap 3.5s ease infinite;
  }
  
  .last-drop {
    width: 10px;
    height: 10px;
    display: block;
    position: absolute;
    bottom: 30px;
    left: 8px;
    border-radius: 50%;
    background: #ab4d37;
    animation: last-drop 3.5s ease infinite;
  }
  
  #hot-coffee-down {
    transform: translateY(0);
    animation: hot-coffee-down 3.5s ease infinite;
  }
  
  .olhos {
    animation: olhos 3.5s ease infinite;
  }
  
  #boca-encher {
    animation: boca-encher 3.5s ease infinite;
  }
  
  #boca-sopro {
    opacity: 0;
    animation: boca-sopro 3.5s ease infinite;
  }
  
  #boca-pronto {
    opacity: 0;
    animation: boca-pronto 3.5s ease infinite;
  }
  
  .svg-machine {
    width: 100%;
    height: 100%;
  }
  
  .stars {
    display: block;
    position: absolute;
    width: 14px;
    height: 14px;
    border-radius: 20%;
    background: #f9d67d;
    opacity: 0;
    bottom: 0;
    left: 0;
  }
  
  .star1 {
    opacity: 0;
    transform: translate(500px, -120px) scale(0.2);
    animation: star1 3.5s ease infinite;
  }
  
  .star2 {
    opacity: 0;
    transform: translate(280px, -170px) scale(0.2);
    animation: star2 3.5s ease infinite;
  }
  
  .star3 {
    opacity: 0;
    transform: translate(500px, -250px) scale(0.2);
    animation: star3 3.5s ease infinite;
  }
  
  /* Floor */
  .floor {
    width: 400px;
    height: 10px;
    bottom: 6px;
    left: 190px;
    z-index: 0;
  }
  
  .svg-floor {
    width: 100%;
    height: 100%;
  }
  
  /* MUG 1 */
  
  @keyframes new-mug {
    0% {
      opacity: 1;
      right: 220px;
    }
  
    21.25% {
      opacity: 1;
      right: 220px;
    }
    21.30% {
      opacity: 0;
      right: 100px;
    }
    31.25% {
      opacity: 0;
      right: 100px;
    }
  
    34% {
      opacity: 1;
    }
  
    37.5% {
      right: 220px;
    }
    100% {
      opacity: 1;
      right: 220px;
    }
  }
  
  /* MUG 2 */
  
  @keyframes jump-to-machine {
    0% {
      opacity: 0;
    }
  
    21.2% {
      opacity: 0;
    }
  
    21.25% {
      opacity: 1;
      bottom: 6px;
      right: 220px;
    }
  
    39.95% {
